var routeEnter = require('routeEnter');

module.exports = Object.assign({
    path: "common",
    breadcrumbName: "系统",
    getChildRoutes(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./routes/login"),
                require("./routes/register"),
                require("./routes/forgotPwd")
            ])
        })
    }
}, routeEnter);


