var routeEnter = require('routeEnter');

module.exports = Object.assign({
    path: "entrust",
    breadcrumbName: "委托",
    getComponent: function(nextState, cb) {
        require.ensure([], function(require) {
            cb(null, require("./components/entrust.jsx"))
        }, 'entrust')
    }
}, routeEnter);


